import React from 'react';
import style from '../assets/scss/style.module.scss';
const MovieRankList = ({boxOfficeResult}) => {
    return (
        <table className={style.table}>
            <thead>
                <tr>
                    <th className={style.textCenter}></th>
                    <th className={style.textCenter}></th>
                    <th className={style.textCenter}></th>
                    <th className={style.textCenter}></th>
                    <th className={style.textCenter}> </th>
                    <th className={style.textCenter}> </th>
                </tr>
            </thead>
            <tbody>
                {boxOfficeResult.dailyBoxOfficeList.map((item, index) => (
                    <tr key={index}>
                        <td className={style.textCenter}>{item.rank}</td>
                        <td className={style.textCenter}>{item.movieNm}</td>
                        <td className={style.textRight}>{Number(item.audiCnt).toLocaleString()}</td>
                        <td className={style.textRight}>{Number(item.salesAmt).toLocaleString()}</td>
                        <td className={style.textRight}>{Number(item.audiAcc).toLocaleString()}</td>
                        <td className={style.textRight}>{Number(item.salesAcc).toLocaleString()}</td>
                    </tr>
                ))}
            </tbody>
        </table>
    );
};

MovieRankList.defaultProps = {
    boxOfficeResult : {
        dailyBoxOfficeList : []
    }
}

export default MovieRankList;