import React from 'react';
import { useNavigate } from 'react-router-dom';
const Top = ({targetDt}) => {
    const navigate = useNavigate();

    const onDateChange = (e) =>{
        e.preventDefault();
        navigate('/' + e.currentTarget.value);
    };
    return (
        <header>
            <h1> </h1>
            <hr/>
            <form>
                <input type="date" className='form-control' placeholder="" defaultValue={targetDt} onChange={onDateChange} />
                <label> </label>
            </form>
        </header>
    );
};

export default Top;